.aboutCompo
{
    position: relative;
    height: 100%;
    min-height: 700px;
    text-align: center;
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    background: rgba(14, 223, 31, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-left: 50px;
    padding-right: 50px;
}
.aboutCompo h1
{
    margin-top: 0;
    font-size: 40px;
}
.aboutCompo p
{
    font-size: 30px;
    letter-spacing: 5px;
    word-spacing: 5px;
    color: #054225;
    margin: 0;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
}
@media (max-width: 768px)
{
   .aboutCompo p
   {
       font-size: 100%;
       
   }
   .aboutCompo
   {
    padding-left: 20px;
    padding-right: 20px;
   }
}