.mainList
{
  position:relative;
  display:flex;
  width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
  
  
   
    
}
.topicList
{
   
    font-family: 'Roboto', sans-serif;
    word-spacing: 0px;
    text-align:center;
    width: 100%;
  
    
}
.imgTopic
{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin-bottom: 5px;
    
    
}
.h1Topic
{
    width: 100%;
    text-align:center;
    color: #00FF96;
    text-shadow: -1px 0 #054225, 0 1px #054225, 1px 0 #054225, 0 -1px #054225;
}
.pTopic
{
    width: 100%;
    text-align:center;
    margin-bottom: 30px;
}
.buttonTopic
{
    text-decoration: none;
    color: #00FF96;
    background-color: #054225;
    border: solid 2px #00FF96;
    padding: 10px;
    border-radius: 20%;
    
    
    
}
.buttonTopic:hover
{
    background:#00FF96;
    color: #054225;
    border-color: #054225; 
}
.buttonTopic:active
{
    padding: 9px;
    
}


@media (max-width: 768px)
{
    .mainList
    {
        flex-direction: column;
        
        margin: 0;
        margin-bottom: 50px;
    }
    .imgTopic
    {
        margin-top: 50px;
    }
}