.baner-bg
{
    position: relative;

    margin:0;
    padding:0;
    

}
.baner-bg-item
{
    margin:0;
    position: relative;
    width: 100%;
    height: 800px;
    z-index: 1;
    box-shadow: 0px 1px 10px 1px black ;
   
    
}
.baner-bg p
{
   
    position: absolute;
    top: 250px;
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
    font-size: 120px;
    font-weight: 700;
    text-align: center;
    margin:0;
    width: 100%;
    z-index: 3000;
    
    
    
    
    
}
@media (max-width: 768px)
{
    .baner-bg-item,
    .baner-bg p
    {
        width: 100%;
        font-size: 200%;
        
        
    }
    .baner-bg p
    {
        top: 65px;
        
        
    }
    .baner-bg-item
    {
        height: 200px;
    }
}
@media (min-width: 769px)
{
    .baner-bg-item,
    .baner-bg p
    {
        width: 100%;
        font-size: 500%;
        
    }
    
}