.footer
{
    position: relative;
    height:200px;
    background-color: #125e54;
    display:flex;
    align-items:center;
    justify-content: center;
    width: 100%;

    
    
    

}
.footerImage
{
    width: 60px;
    border-radius: 50%;
    margin: 50px;
    
}
@media (max-width: 768px)
{
    
    .footer
    {
        width: 100%;
       
    }
    .footerImage
    {
        margin: 10px;
    }
}