.header
{
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    background: #00FF96;
    height: 56px;
    box-shadow: 0px 0px 4px 0px grey ;
    z-index: 9000;
    
}
.header_navi
{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}
.header_logo
{
    margin-left: 1rem;
    
}
.header_logo a
{
 color:#054225;
 text-decoration: none;
 font-size: 1.5rem;
}
.header_logo a:hover,
.header_logo a:active
{
    text-shadow: -0.1px 0 #054225, 0 0.1px #054225, 0.1px 0 #054225, 0 -0.1px #054225;
}
.header_item ul
{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}
.header_item li
{
    padding: 0 0.5rem;
    
}
.header_item li a
{
    text-decoration: none;
    color:#054225;
    font-weight: 700;
}

.header_item a:hover,
.header_item a:active
{
    color: #fab73c;
}
.spacer
{
    flex: 1;
}

@media (max-width: 768px)
{
    .header_item
    {
        display: none;
    }
    
}
@media (min-width: 769px)
{
    .header-toggle
    {
        display:none;
    }
    .header_logo
    {
        margin-left: 0;
    }
}